import axios from "axios";
import { useEffect, useState } from "react";
import { toast, ToastContainer } from "react-toastify";
import {
	Button,
	Card,
	CardBody,
	CardHeader,
	Col,
	Container,
	Row,
} from "shards-react";
import { ENDPOINT } from "../api/Api";
import PageTitle from "../components/common/PageTitle";
import Confirmation from "../components/confirm/confirm.component";

const Patients = () => {
	const [open, setOpen] = useState(false);
	const [id, setId] = useState("");
	const [patients, setPatients] = useState([]);

	useEffect(() => {
		getPatients();
	}, []);

	const handleDelete = (id) => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.delete(`${ENDPOINT}/patient`, config)
			.then((res) => {
				getPatients();
				setId("");
				toast.success(res.data.message);
			})
			.catch((error) => {
				setId("");
				toast.success(error.data.message);
			});
	};
	const callBack = (id) => {
		handleDelete(id);
	};

	const getPatients = () => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.get(`${ENDPOINT}/patient`, config)
			.then((res) => {
				setPatients(res.data.patients);
				console.log(res.data.patients);
			})
			.catch((error) => {
				console.log("something went wrong", error);
			});
	};

	return (
		<Container fluid className='main-content-container px-4'>
			{/* Page Header */}
			<Row noGutters className='page-header py-4'>
				<PageTitle sm='4' title='Patients' className='text-sm-left' />
			</Row>
			<ToastContainer />
			<Confirmation
				open={open}
				callback={callBack}
				close={() => setOpen(false)}
				id={id}
			/>
			<Row>
				<Col>
					<Card small className='mb-4 overflow-hidden'>
						<CardHeader className='bg-dark'>
							<h6 className='m-0 text-white'>All Patients</h6>
						</CardHeader>
						<CardBody className='bg-light p-0 pb-3'>
							<table className='table table-light mb-0'>
								<thead className='thead-light'>
									<tr>
										<th scope='col' className='border-0'>
											Full Name
										</th>
										<th scope='col' className='border-0 '>
											userName
										</th>
										<th scope='col' className='border-0 desktop'>
											Email
										</th>
										<th scope='col' className='border-0 desktop'>
											Phone Number
										</th>

										<th scope='col' className='border-0'>
											Delete
										</th>
									</tr>
								</thead>
								<tbody>
									{patients[0] &&
										patients.map((patient, idx) => (
											<tr key={idx}>
												<td>
													Dr. {patient.firstName} {patient.lastName}
												</td>
												<td> {patient.userName}</td>
												<td className='desktop'> {patient.email}</td>

												<td className='desktop'> {patient.phoneNumber}</td>

												<td>
													<Button
														theme='danger'
														id={patient._id}
														onClick={() => {
															setOpen(true);
															setId(patient._id);
														}}
													>
														<span style={{ marginRight: "10px" }}>
															<i className='material-icons'>clear</i>
														</span>
														Delete
													</Button>
												</td>
											</tr>
										))}
								</tbody>
							</table>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default Patients;
