import axios from "axios";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ToastContainer } from "react-toastify";
import {
	Button,
	Card,
	Col,
	Container,
	FormSelect,
	Modal,
	Row,
} from "shards-react";
import { ENDPOINT } from "../api/Api";
import PageTitle from "../components/common/PageTitle";
import { doctorsAdded } from "../store/doctors-reducer";
import { formatAMPM } from "../utils/formatAMPM";

const ClosedDays = () => {
	const [filter, setFilter] = useState(0);
	const [open, setOpen] = useState(false);
	const [shifts, setShifts] = useState([]);
	const [date, setDate] = useState(new Date());

	const doctor = useSelector((state) => state.user[0]);

	const dispatch = useDispatch();

	useEffect(() => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.get(`${ENDPOINT}/doctor`, config)
			.then((res) => {
				dispatch(
					doctorsAdded(res.data.filter((user) => user.role !== "admin"))
				);
				doctor && doctor.closedDays
					? setShifts(doctor.closedDays)
					: setShifts([]);
			})
			.catch((error) => {
				console.log("unable to fetch doctors", error);
			});
	}, [doctor, fetch, filter]);

	const changeTab = (e) => {
		setFilter(e.target.value);
	};

	return (
		<Container fluid className='main-content-container px-4 pb-4'>
			<ToastContainer />
			<Row noGutters className='page-header py-4'>
				<PageTitle
					sm='4'
					title='My Closed Day'
					subtitle='Doctors'
					className='text-sm-left'
				/>
			</Row>

			<Row>
				{/* Editor */}
				<Col lg='12' md='12'>
					<Card small className='mb-3 mt-4 '>
						{shifts[0] && (
							<Row
								style={{
									padding: "30px 50px",
									display: "grid",
									gridTemplateColumns: `repeat(${shifts.length},1fr)`,
									gridGap: "20px",
									height: "85px",
									paddingBottom: "0",
								}}
								className='desktop'
							>
								{shifts[0] &&
									shifts.map((day, index) => (
										<Button
											theme='primary'
											className='mb-2 mr-1  w100 hover-red'
											outline={index === parseInt(filter) ? false : true}
											value={index}
											onClick={changeTab}
											name={day}
											key={index}
										>
											{new Date(day.day).getDate() +
												"-" +
												new Date(day.day).getMonth() +
												"-" +
												new Date(day.day).getFullYear()}
										</Button>
									))}
							</Row>
						)}
						<Row
							className='mobile'
							style={{ padding: "0 30px", paddingTop: "30px" }}
						>
							<Row lg='8'>
								<Col md='8' sm='9' className='form-group'>
									<FormSelect id='Days' onChange={changeTab}>
										{shifts[0] ? (
											shifts.map((day, index) => (
												<option
													value={index}
													onClick={changeTab}
													name={day}
													key={index}
												>
													{new Date(day.day).getDate() +
														"-" +
														new Date(day.day).getMonth() +
														"-" +
														new Date(day.day).getFullYear()}
												</option>
											))
										) : (
											<option>No Closed Date</option>
										)}
									</FormSelect>
								</Col>
							</Row>
						</Row>
						<hr />
						<Row
							style={{
								padding: "10px 30px",
							}}
						>
							<Modal show />
							{shifts[0] ? (
								<Col>
									<h4>Time Slots</h4>
								</Col>
							) : (
								<Col>
									<h4>No Closed Days</h4>
								</Col>
							)}
						</Row>
						<Row className='time-slot-container'>
							{shifts[0] &&
								shifts[filter].closedHours.map((schedule, index) => (
									<Button
										theme='primary'
										key={index}
										value={schedule._id}
										className='mb-2 mr-1 time-slots'
									>
										{`${formatAMPM(
											schedule.startHour.hour,
											schedule.startHour.minute
										)}
										-
										${formatAMPM(schedule.endHour.hour, schedule.endHour.minute)}`}
									</Button>
								))}
						</Row>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default ClosedDays;
