import axios from "axios";
import PropTypes from "prop-types";
import React, { useEffect, useState } from "react";
import {
	Button,
	Card,
	CardHeader,
	Col,
	Form,
	FormInput,
	FormSelect,
	FormTextarea,
	ListGroup,
	ListGroupItem,
	Row,
} from "shards-react";
import { ENDPOINT } from "../../api/Api";

const UserAccountForm = ({ title, handleChange, handleSubmit, state }) => {
	const [departments, setDepartments] = useState([]);
	useEffect(() => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.get(`${ENDPOINT}/department/`, config)
			.then((res) => {
				setDepartments(res.data.departments);
				handleChange({
					target: { name: "department", value: res.data.departments[0]._id },
				});
			})
			.catch((error) => {
				console.log("something went wrong", error);
			});
	}, []);
	return (
		<Card small className='mb-4'>
			<CardHeader className='border-bottom'>
				<h6 className='m-0'>{title}</h6>
			</CardHeader>
			<ListGroup flush>
				<ListGroupItem className='p-3'>
					<Row>
						<Col>
							<Form>
								<Row form>
									{/* First Name */}
									<Col md='6' className='form-group'>
										<label htmlFor='feFirstName'>First Name</label>
										<FormInput
											id='feFirstName'
											placeholder='First Name'
											name='firstName'
											onChange={handleChange}
										/>
									</Col>
									{/* Last Name */}
									<Col md='6' className='form-group'>
										<label htmlFor='feLastName'>Last Name</label>
										<FormInput
											id='feLastName'
											placeholder='Last Name'
											name='lastName'
											onChange={handleChange}
										/>
									</Col>

									{/* Email */}
									<Col md='6' className='form-group'>
										<label htmlFor='feEmail'>Email</label>
										<FormInput
											type='email'
											id='feEmail'
											placeholder='Email Address'
											name='email'
											onChange={handleChange}
											autoComplete=''
										/>
									</Col>
									<Col md='6' className='form-group'>
										<label htmlFor='feUsername'>Username</label>
										<FormInput
											type='text'
											id='feUsername'
											placeholder='Username'
											name='userName'
											onChange={handleChange}
										/>
									</Col>
									<Col md='6' className='form-group'>
										<label htmlFor='fePhone'>Phone Number</label>
										<FormInput
											type='text'
											id='fePhone'
											placeholder='Phone Number'
											name='phoneNumber'
											onChange={handleChange}
										/>
									</Col>
									<Col md='6' className='form-group'>
										<label htmlFor='feEmploymentType'>Employment Type</label>
										<FormSelect
											id='feEmploymentType'
											name='employmentType'
											onChange={handleChange}
										>
											<option>part-time</option>
											<option>full-time</option>
										</FormSelect>
									</Col>
									<Col md='6' className='form-group'>
										<label htmlFor='feEmploymentType'>Department</label>
										<FormSelect
											id='feDepartment'
											name='department'
											onChange={handleChange}
										>
											{departments.map((dept, idx) => (
												<option key={idx} value={dept._id}>
													{dept.deptName}
												</option>
											))}
										</FormSelect>
									</Col>
									<Col md='6' className='form-group'>
										<label htmlFor='feGender'>Gender</label>
										<FormSelect
											id='feGender'
											name='gender'
											onChange={handleChange}
										>
											<option value='male'>Male</option>
											<option value='female'>Female</option>
										</FormSelect>
									</Col>
									<Col md='6' className='form-group'>
										<label htmlFor='feSpecialization'>Specialization</label>
										<FormInput
											type='text'
											id='feSpecialization'
											placeholder='specialization'
											name='specialization'
											onChange={handleChange}
										/>
									</Col>
									<Col md='6' className='form-group'>
										<label htmlFor='feBirthDate'>Birth Date</label>
										<FormInput
											type='date'
											id='feBirthDate'
											placeholder='specialization'
											name='birthDate'
											onChange={handleChange}
										/>
									</Col>
									{/* Password */}
									<Col md='6' className='form-group'>
										<label htmlFor='fePassword'>Password</label>
										<FormInput
											type='password'
											id='fePassword'
											placeholder='Password'
											name='password'
											onChange={handleChange}
										/>
									</Col>
									<Col md='12' className='form-group'>
										<label htmlFor='feTextArea'>Profile</label>
										<FormTextarea
											type='textarea'
											id='feTextArea'
											placeholder='profile'
											name='profile'
											onChange={handleChange}
										/>
									</Col>
								</Row>

								<Button
									theme='accent'
									disabled={state.loading}
									onClick={handleSubmit}
								>
									Create Account
								</Button>
							</Form>
						</Col>
					</Row>
				</ListGroupItem>
			</ListGroup>
		</Card>
	);
};

UserAccountForm.propTypes = {
	/**
	 * The component's title.
	 */
	title: PropTypes.string,
};

UserAccountForm.defaultProps = {
	title: "Account Details",
};

export default UserAccountForm;
