import axios from "axios";
import React, { useEffect, useState } from "react";
import { toast, ToastContainer } from "react-toastify";
import {
	Button,
	Card,
	Col,
	Container,
	FormSelect,
	Modal,
	Row,
} from "shards-react";
import { ENDPOINT } from "../api/Api";
import { apiCall } from "../api/ApiCall";
import PageTitle from "../components/common/PageTitle";
import TimeSelector from "../components/TimeSelector/TimeSelector.component";
import { formatAMPM } from "../utils/formatAMPM";

const EditRegularDoctorSchedule = () => {
	let weekDays = [
		"Sunday",
		"Monday",
		"Tusday",
		"Wednsday",
		"Thursday",
		"Friday",
		"Sararday",
	];
	var d = new Date();
	var dayName = weekDays[d.getDay()];
	const [filter, setFilter] = useState({ day: dayName, index: d.getDay() });
	const [open, setOpen] = useState(false);
	const [shifts, setShifts] = useState([]);

	useEffect(() => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.get(`${ENDPOINT}/working-days/`, config)
			.then((res) => {
				setShifts(
					res.data.workingDays.filter(
						(schedule) => schedule.day === filter.index
					)
				);
			})
			.catch((error) => {
				console.log("something went wrong", error);
			});
	}, [filter]);

	const toggle = () => {
		setOpen(!open);
	};
	const handleChange = (newSlot) => {
		let temp = { ...shifts[0] };

		function isObjectEmpty(obj) {
			return Object.keys(obj).length === 0;
		}
		console.log(isObjectEmpty(temp));
		if (isObjectEmpty(temp)) {
			temp = {
				day: filter.index,
				suspense: false,
				shifts: [newSlot],
			};
			setShifts([temp]);
		} else {
			console.log(temp);

			temp.shifts.push(newSlot);
		}
	};

	const handleDelete = (e) => {
		let index = shifts[0].shifts.findIndex((sh) => sh._id === e.target.value);
		let temp = { ...shifts[0] };
		console.log(temp.shifts.splice(index, 1));
		setShifts([temp]);
	};

	const handleSave = () => {
		if (shifts[0] && shifts[0]._id) {
			toast.promise(
				apiCall(
					"put",
					shifts[0],
					`/working-days/${shifts[0]._id}`,
					onSuccess,
					onFaileure
				),
				{
					pending: "Saving ...",
				}
			);
		} else {
			toast.promise(
				apiCall("post", shifts[0], `/working-days/`, onSuccess, onFaileure),
				{
					pending: "Saving ...",
				}
			);
		}
	};

	const onFaileure = (error) => {
		console.log(error);
	};

	const onSuccess = (data) => {
		console.log(data, "here is succss");
	};

	const changeTab = (e) => {
		setFilter({ day: e.target.name, index: parseInt(e.target.value) });
	};

	return (
		<Container fluid className='main-content-container px-4 pb-4'>
			<ToastContainer />
			<Row noGutters className='page-header py-4'>
				<PageTitle
					sm='4'
					title="Edit Regular Doctor's Schedule"
					subtitle='Doctors'
					className='text-sm-left'
				/>
			</Row>
			<TimeSelector
				open={open}
				toggle={toggle}
				day={filter.day.toLocaleLowerCase()}
				handleChange={handleChange}
			/>
			<Row>
				{/* Editor */}
				<Col lg='12' md='12'>
					<Card small className='mb-3 mt-4 '>
						<Row
							style={{
								padding: "30px 50px",
								display: "grid",
								gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr 1fr 1fr",
								gridGap: "20px",
								height: "85px",
								paddingBottom: "0",
							}}
							className='desktop'
						>
							{weekDays &&
								weekDays.map((day, index) => (
									<Button
										theme='primary'
										className='mb-2 mr-1  w100 hover-red'
										outline={filter.day !== day}
										value={index}
										onClick={changeTab}
										name={day}
										key={index}
									>
										{day}
									</Button>
								))}
						</Row>
						<Row
							style={{
								padding: "30px 30px",

								gridGap: "20px",
								height: "85px",
							}}
							className='mobile'
						>
							<Row lg='12'>
								<Col md='8' sm='9' className='form-group'>
									<FormSelect id='Days' onChange={changeTab}>
										{weekDays &&
											weekDays.map((day, index) => (
												<option
													value={index}
													onClick={changeTab}
													name={day}
													key={index}
												>
													{day}
												</option>
											))}
									</FormSelect>
								</Col>
							</Row>
						</Row>
						<hr />
						<Row
							style={{
								padding: "10px 30px",
							}}
						>
							<Modal show />
							<Col>
								<h4>Time Slots</h4>
							</Col>
							<Col>
								<Button
									theme='success'
									style={{ float: "right", width: "auto" }}
									onClick={toggle}
								>
									Add New Slot
								</Button>
							</Col>
						</Row>
						<hr />

						<Row className='time-slot-container'>
							{shifts[0] &&
								shifts[0].shifts.map((schedule, index) => (
									<Button
										theme='primary'
										key={index}
										value={schedule._id}
										className='mb-2 mr-1 time-slots'
										onClick={handleDelete}
									>
										{`${formatAMPM(
											schedule.startHour.hour,
											schedule.startHour.minute
										)}
										-
										${formatAMPM(schedule.endHour.hour, schedule.endHour.minute)}`}
										<button value={schedule._id} className='close'>
											X
										</button>
									</Button>
								))}
						</Row>
						<hr />
						<Button theme='success' className='save' onClick={handleSave}>
							Save Changes
						</Button>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default EditRegularDoctorSchedule;
