import axios from "axios";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast, ToastContainer } from "react-toastify";
import {
	Button,
	Card,
	CardBody,
	CardHeader,
	Col,
	Container,
	Row,
} from "shards-react";
import { ENDPOINT } from "../api/Api";
import PageTitle from "../components/common/PageTitle";
import Confirmation from "../components/confirm/confirm.component";
import { doctorsAdded } from "../store/doctors-reducer";

const Doctors = () => {
	const doctors = useSelector((state) => state.doctors);
	const [open, setOpen] = useState(false);
	const [id, setId] = useState("");
	const dispatch = useDispatch();

	useEffect(() => {
		getDoctors();
	}, []);

	const handleDelete = (id) => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.delete(`${ENDPOINT}/doctor/${id}`, config)
			.then((res) => {
				getDoctors();
				setId("");
				toast.success(res.data.message);
			})
			.catch((error) => {
				setId("");
				toast.success(error.data.message);
			});
	};
	const callBack = (id) => {
		handleDelete(id);
	};

	const getDoctors = () => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.get(`${ENDPOINT}/doctor`, config)
			.then((res) => {
				dispatch(
					doctorsAdded(res.data.filter((user) => user.role !== "admin"))
				);
			})
			.catch((error) => {
				console.log("something went wrong", error);
			});
	};

	return (
		<Container fluid className='main-content-container px-4'>
			{/* Page Header */}
			<Row noGutters className='page-header py-4'>
				<PageTitle sm='4' title='Doctors' className='text-sm-left' />
			</Row>
			<ToastContainer />
			<Confirmation
				open={open}
				callback={callBack}
				close={() => setOpen(false)}
				id={id}
			/>
			<Row>
				<Col>
					<Card small className='mb-4 overflow-hidden'>
						<CardHeader className='bg-dark'>
							<h6 className='m-0 text-white'>All Doctors</h6>
						</CardHeader>
						<CardBody className='bg-light p-0 pb-3'>
							<table className='table table-light mb-0'>
								<thead className='thead-light'>
									<tr>
										<th scope='col' className='border-0'>
											Full Name
										</th>
										<th scope='col' className='border-0 desktop'>
											Specialization
										</th>
										<th scope='col' className='border-0 mobile'>
											Username
										</th>
										<th scope='col' className='border-0 desktop'>
											Email
										</th>
										<th scope='col' className='border-0 desktop'>
											Phone Number
										</th>

										<th scope='col' className='border-0 '>
											Delete
										</th>
									</tr>
								</thead>
								<tbody>
									{doctors.map((doctor, idx) => (
										<tr key={idx}>
											<td>
												Dr. {doctor.firstName} {doctor.lastName}
											</td>
											<td className='desktop'> {doctor.specialization}</td>
											<td className='mobile'> {doctor.userName}</td>
											<td className='desktop'> {doctor.email}</td>

											<td className='desktop'> {doctor.phoneNumber}</td>

											<td>
												<Button
													theme='danger'
													id={doctor._id}
													onClick={() => {
														setOpen(true);
														setId(doctor._id);
													}}
												>
													<span style={{ marginRight: "10px" }}>
														<i className='material-icons'>clear</i>
													</span>
													Delete
												</Button>
											</td>
										</tr>
									))}
								</tbody>
							</table>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default Doctors;
