/* eslint jsx-a11y/anchor-is-valid: 0 */

import axios from "axios";
import React, { useState, useEffect } from "react";
import {
	Container,
	Row,
	Col,
	Card,
	CardHeader,
	CardBody,
	Button,
} from "shards-react";
import { ENDPOINT } from "../api/Api";
import PageTitle from "../components/common/PageTitle";

import { useSelector } from "react-redux";

const DocAppointments = () => {
	const [filter, setFilter] = useState(false);
	console.log("user");
	const user = useSelector((state) => state.user[0]);
	const [appointments, setAppointments] = useState([]);

	useEffect(() => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.get(`${ENDPOINT}/appointment/get-by-doctor/${user._id}`, config)
			.then((res) => {
				console.log(res.data.appointments);
				let filterd = [];
				filterd = filter
					? res.data.appointments.filter(
							(paitent) =>
								new Date(paitent.appointmentDate).getMonth() ===
									new Date().getMonth() &&
								new Date(paitent.appointmentDate).getDay() ===
									new Date().getDay() &&
								new Date(paitent.appointmentDate).getFullYear() ===
									new Date().getFullYear()
					  )
					: res.data.appointments;
				setAppointments([...filterd]);
			})
			.catch((error) => {
				console.log("something went wrong", error);
			});
	}, [user, filter]);

	return (
		<Container fluid className='main-content-container px-4'>
			{/* Page Header */}
			<Row noGutters className='page-header py-4'>
				<PageTitle
					sm='4'
					title='Appointments'
					subtitle='Doctors'
					className='text-sm-left'
				/>
			</Row>
			<Row>
				<Col>
					<Card small className='mb-4'>
						<CardHeader className='border-bottom'>
							<Button
								outline={filter}
								size='sm'
								theme='primary'
								className='mb-2 mr-1'
								onClick={() => setFilter(false)}
							>
								Upcoming
							</Button>
							<Button
								size='sm'
								outline={!filter}
								theme='primary'
								className='mb-2 mr-1'
								onClick={() => setFilter(true)}
							>
								Today
							</Button>
						</CardHeader>
						<CardBody className='p-0 pb-3'>
							<table className='table mb-0'>
								<thead className='bg-light'>
									<tr>
										<th scope='col' className='border-0 desktop'>
											#
										</th>
										<th scope='col' className='border-0'>
											Name
										</th>

										<th scope='col' className='border-0 desktop'>
											Gender
										</th>
										<th scope='col' className='border-0 desktop'>
											Purpose
										</th>
										<th scope='col' className='border-0'>
											Date
										</th>
										<th scope='col' className='border-0'>
											Time
										</th>
									</tr>
								</thead>
								<tbody>
									{appointments.map((paitent, index) => (
										<tr key={index}>
											<td>{index + 1}</td>

											<td>{paitent.patientId}</td>
											<td>Male</td>
											<td>{paitent.reasonForVisit}</td>
											<td>
												{new Date(paitent.appointmentDate).getDate()}-
												{new Date(paitent.appointmentDate).getMonth()}-
												{new Date(paitent.appointmentDate).getFullYear()}
											</td>
											<td>
												{paitent.appointmentTime.hour}:
												{paitent.appointmentTime.minute}
											</td>
										</tr>
									))}
								</tbody>
							</table>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default DocAppointments;
