import React, { useState, useEffect } from "react";
import { Col, Container, Row } from "shards-react";
import DepartmentList from "../components/departments/DepartmentList";
import PageTitle from "../components/common/PageTitle";
import CreateDepartment from "../components/departments/CreateDepartment";
import axios from "axios";
import { ENDPOINT } from "../api/Api";
import { useDispatch, useSelector } from "react-redux";
import { departmentsAdded } from "../store/departments-reducer";
import PropTypes from "prop-types";
import { toast, ToastContainer } from "react-toastify";
import { Button, ButtonGroup, Card, CardBody, CardHeader } from "shards-react";
import Confirmation from "../components/confirm/confirm.component";
import EditDepartment from "../components/EditDepartment/EditDepartment.component";

const Departments = () => {
	const [open, setOpen] = useState(false);
	const [openEditor, setOpenEditor] = useState(false);
	const [id, setId] = useState("");
	const [name, setName] = useState("");

	const handleDelete = (id) => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.delete(`${ENDPOINT}/department/${id}`, config)
			.then((res) => {
				setId("");
				setFetch(!fetch);
				toast.success(res.data.message);
			})
			.catch((error) => {
				setId("");
				toast.error(error.message);
			});
	};
	const callBack = (id) => {
		handleDelete(id);
	};

	const departmets = useSelector((state) => state.departments);

	const [fetch, setFetch] = useState(false);

	const dispatch = useDispatch();

	useEffect(() => {
		const config = {
			headers: {
				Authorization: `${sessionStorage.getItem("token")}`,
			},
		};

		axios
			.get(`${ENDPOINT}/department/`, config)
			.then((res) => {
				dispatch(departmentsAdded(res.data.departments));
			})
			.catch((error) => {
				console.log("something went wrong", error);
			});
	}, [fetch]);
	return (
		<Container fluid className='main-content-container px-4'>
			{/* Page Header */}
			<Row noGutters className='page-header py-4'>
				<PageTitle sm='4' title='Departments' className='text-sm-left' />
			</Row>
			<Confirmation
				open={open}
				callback={callBack}
				close={() => setOpen(false)}
				id={id}
			/>
			<ToastContainer />
			<EditDepartment
				open={openEditor}
				toggle={() => setOpenEditor(!openEditor)}
				close={() => setOpenEditor(false)}
				id={id}
				name={name}
				setFetch={setFetch}
				fetch={fetch}
			/>
			<CreateDepartment setFetch={setFetch} fetch={fetch} />
			<Row>
				<Col>
					<Card small className='mb-4 overflow-hidden'>
						<CardHeader className='bg-dark'>
							<h6 className='m-0 text-white'>All Departments</h6>
						</CardHeader>
						<CardBody className='bg-light p-0 pb-3'>
							<table className='table table-light mb-0'>
								<thead className='thead-light'>
									<tr>
										<th scope='col' className='border-0'>
											Department Name
										</th>

										<th scope='col' className='border-0'>
											Delete
										</th>
										<th scope='col' className='border-0'>
											Edit
										</th>
									</tr>
								</thead>
								<tbody>
									{departmets.map((doctor, idx) => (
										<tr key={idx}>
											<td> {doctor.deptName}</td>

											<td>
												<Button
													theme='danger'
													id={doctor._id}
													onClick={() => {
														setOpen(true);
														setId(doctor._id);
													}}
												>
													<span style={{ marginRight: "10px" }}>
														<i className='material-icons'>clear</i>
													</span>
													Delete
												</Button>
											</td>
											<td>
												<Button
													theme='warning'
													id={doctor._id}
													onClick={() => {
														setOpenEditor(true);
														setId(doctor._id);
														setName(doctor.deptName);
													}}
												>
													<span style={{ marginRight: "10px" }}>
														<i className='material-icons'>edit</i>
													</span>
													Edit
												</Button>
											</td>
										</tr>
									))}
								</tbody>
							</table>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default Departments;
